.array-container {
  position: absolute;
  left: 0;
  /* top: 100px; */ }

.array-bar {
  width: 5px;
  display: inline-block;
  margin: 0 10px;
  position: relative; }

.array-bar .tooltiptext {
  visibility: hidden;
  width: 60px;
  background: var(--background) !important;
  color: var(--foreground) !important;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  font-weight: bold;
  /* Position the tooltip */
  position: absolute;
  z-index: 1;
  top: -10px;
  left: 20px; }

.array-bar:hover .tooltiptext {
  visibility: visible; }

/* Style the header */
.header-sort {
  grid-area: header;
  color: var(--foreground) !important;
  padding: 10px;
  text-align: center; }

.header-sort > div {
  display: inline-block;
  display: -moz-inline-box;
  *display: inline;
  /* For IE7 */
  zoom: 1;
  /* Trigger hasLayout */
  width: 20%;
  text-align: center; }

/* The grid container */
.grid-container-sort {
  min-height: calc(100vh - 70px);
  display: grid;
  grid-template-areas: "header header header" "left middle  right" "footer footer footer";
  grid-column-gap: 5px;
  border: 10px solid white;
  grid-template-columns: 1fr 8fr 1fr;
  /* grid-column-gap: 10px; - if you want gap between the columns */ }

.left-sort,
.middle-sort,
.right-sort {
  border: 1px solid #222;
  padding: 5px;
  background-color: white;
  /*height: 300px; /* Should be removed. Only for demonstration */ }

/* Style the left column */
.left-sort {
  grid-area: left; }

/* Style the middle column */
.middle-sort {
  grid-area: middle;
  min-height: 60vh;
  min-width: 60vw;
  max-width: 100vw; }

/* Style the right column */
.right-sort {
  grid-area: right; }

.input-range {
  margin: 5 5px; }

.input-range:hover .tooltiptext {
  visibility: visible; }

.input-range .tooltiptext {
  font-weight: bold;
  visibility: hidden;
